import Vue from 'vue';
import App from './App.vue';
import router from './router';
import './assets/font/iconfont.js';

//Vant
import { Button, Cell, CellGroup, List, Popup, Toast, Field, Sticky } from 'vant';
Vue.use(Button)
  .use(Cell)
  .use(CellGroup)
  .use(List)
  .use(Popup)
  .use(Toast)
  .use(Field)
  .use(Sticky);

Vue.config.productionTip = false;

new Vue({
  router,
  render: h => h(App)
}).$mount('#app');

router.beforeEach((to, from, next) => {
  if (to.meta.title) document.title = to.meta.title;
  next();
});
