<template>
  <div class="container">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="别滑了，没了"
      loading-text="正在认真加载..."
      @load="onLoad"
    >
      <div v-for="post in list" :key="post.id" class="post">
        <div class="post-hd">
          <h2>{{post.title}}</h2>
          <div class="post-date">{{post.date}}</div>
        </div>
        <div v-if="post.thumb" class="post-thumb">
          <img :src="post.thumb" />
        </div>
        <div class="post-content" v-html="post.content"></div>
      </div>
    </van-list>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      list: [],
      loading: false,
      finished: false
    };
  },
  methods: {
    onLoad() {
      setTimeout(() => {
        for (let i = 0; i < 3; i++) {
          var title = "人喜则斯陶",
            thumb = "http://p3.pstatp.com/large/4aeb0002c03c1a33b838",
            content =
              "<p>人喜则斯陶，陶斯咏，咏斯犹，犹斯舞，舞斯愠，愠斯戚，戚斯叹，叹斯辟，辟斯踊矣。</p>";
          if (i % 2 != 0) {
            title =
              "陶斯咏，咏斯犹，犹斯舞，舞斯愠，愠斯戚，戚斯叹，叹斯辟，辟斯踊";
            thumb = "";
            content =
              "<p>后皇嘉树，橘徕服兮。受命不迁，生南国兮。深固难徙，更壹志兮。绿叶素荣，纷其可喜兮。</p><p>曾枝剡棘，圆果抟兮。青黄杂糅，文章烂兮。</p><p>精色内白，类任道兮。纷緼宜修，姱而不丑兮。</p>";
          }
          this.list.push({
            id: parseInt(Math.random() * 100000),
            title: title,
            date: new Date().toDateString(),
            thumb: thumb,
            content: content
          });
        }
        // 加载状态结束
        this.loading = false;
        // 数据全部加载完成
        if (this.list.length >= 16) {
          this.finished = true;
        }
      }, 1000);
    }
  }
};
</script>

